"use client";

import { motion } from "motion/react";
import Link from "next/link";

export default function Focus() {
return (
<section className="relative z-20 px-4 lg:px-8 py-24 lg:py-32 bg-white">
<div className=" mx-auto text-start">
{/* Main Heading */}
<motion.h2
initial={{ opacity: 0, y: 50 }}
animate={{ opacity: 1, y: 0 }}
transition={{ duration: 0.8 }}
className="text-[clamp(40px,8vw,120px)] font-medium leading-tight text-black mb-12 lg:mb-16"
>
FOCUSED ON QUALITY
<br /><span className="text-end block">
    DRIVEN BY CREATIVITY

</span>

</motion.h2>


    {/* CTA Button */}
    <motion.div
      initial={{ opacity: 0, y: 30 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.8, delay: 0.3 }}
       className="flex justify-center" 
    >
      <Link 
        href="/contact"
        className="inline-flex items-center gap-2 bg-black text-white px-8 py-4 rounded-full font-bold text-sm uppercase tracking-wider hover:bg-neutral-800 transition-colors duration-300"
      >
        TELL US ABOUT YOUR PROJECT
        <svg 
          className="w-4 h-4" 
          fill="none" 
          stroke="currentColor" 
          viewBox="0 0 24 24"
        >
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
        </svg>
      </Link>
    </motion.div>
  </div>
</section>


);
}