"use client";

import "./globals.css";
import { ViewTransitions } from "next-view-transitions";
import Header from "../components/layout/Header";
import { ReactLenis } from "lenis/react";

import { saans } from "@/fonts";
import { FooterProvider } from "@/contexts/footer-context";
import Footer from "@/components/layout/Footer";
import LoadingScreen from "@/components/layout/LoadingScreen";
import { InitialLoadProvider } from "@/contexts/initial-load-context";
import type { Metadata } from "next";
import Focus from "@/components/layout/Focus";

import { usePathname } from "next/navigation";

export const metadata: Metadata = {
  title: "My Portfolio",
  description: "A portfolio website showcasing my work",
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const pathname = usePathname();
  const hideFooterAndFocus = pathname === "/works";

  return (
    <ViewTransitions>
      <html lang="en">
        <ReactLenis root>
          <InitialLoadProvider>
            <FooterProvider>
              <body
                className={`${saans.className} antialiased bg-neutral-900 text-neutral-900 overscroll-none`}
              >
                <LoadingScreen />
                <Header />

                {children}

                {!hideFooterAndFocus && <Focus />}
                {!hideFooterAndFocus && <Footer />}
              </body>
            </FooterProvider>
          </InitialLoadProvider>
        </ReactLenis>
      </html>
    </ViewTransitions>
  );
}
